import { create } from 'apisauce'

import { config } from '../../config'

console.log(config.default)

const api = (config) => {
  const { BASE_URL, BASE_HEADERS } = config.default
  return create({
    baseURL: BASE_URL,
    headers: BASE_HEADERS
  })
}

export default api



